<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      </div>
      <div id="loading-container" class="nv-row row hover vs-con-loading__container mb-base">
        <div class="nv-col sm:w-1/2 w-full">
          <p class="info-row">
            <span>{{ $t('LOAN_NUMBER') }}:</span>
            <span>{{ loan.loanNumber | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="loan.loanAccountNumber">
            <span>{{ $t('LOAN_ACCOUNT_NUMBER') }}:</span>
            <span>{{ loan.loanAccountNumber | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('LOAN_TYPE') }}:</span>
            <span>{{ loan.typeName | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('START_DATE') }}:</span>
            <span>{{ loan.startDate | formatUnixDate }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('MATURITY_DATE') }}:</span>
            <span>{{ loan.maturityDate | formatUnixDate }}</span>
          </p>
          <p class="info-row" v-if="loan.paymentStartDate">
            <span>{{ $t('PAYMENT_START_DATE') }}:</span>
            <span>{{ loan.paymentStartDate | formatUnixDate }}</span>
          </p>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <p class="info-row">
            <span>{{ $t('CURRENCY') }}:</span>
            <span>{{ loan.currencyAbbreviation | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('PRINCIPAL_DISBURSED') }}:</span>
            <span :class="negativeNumberStyle(loan.loanAmount)">{{ loan.loanAmount | currency('') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('BALANCE_OUTSTANDING') }}:</span>
            <span :class="positiveNumberStyle(loan.outstandingAmount)">{{ loan.outstandingAmount | currency('') }}</span>
          </p>
          <p class="info-row" v-if="loan.dueAmount">
            <span>{{ $t('DUE_AMOUNT') }}:</span>
            <span>{{ loan.dueAmount | currency('') | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="loan.principalInterestRate">
            <span>{{ $t('PRINCIPAL_INTEREST_RATE') }}:</span>
            <span>{{ loan.principalInterestRate | placeholder('-') }}%</span>
          </p>
          <p class="info-row" v-if="loan.penaltyInterestRate">
            <span>{{ $t('PENALTY_INTEREST_RATE') }}:</span>
            <span>{{ loan.penaltyInterestRate | placeholder('-') }}%</span>
          </p>
        </div>
      </div>

      <vs-tabs>
        <vs-tab :label="$t('REPAYMENT_SCHEDULE')">
          <loan-repayment @show-alert="showAlert" :loanNumber="loanNumber"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import LoanService from '@/services/loan/LoanService'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'
import LoanRepayment from '@/views/loan/loan-root/loan-view/Repayment'

export default {
  name: 'loan-view',
  mixins: [AlertMixin, LoadingMixin, NumberStyleMixin],
  components: {
    LoanRepayment
  },
  data: function () {
    return {
      loan: {},
      loanNumber: this.$route.params.id,
      popupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      LoanService.loanDetail({
        loanNumber: this.$route.params.id
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.loan = response.data.loan || {}
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
