<template>
  <div id="repayment-loading-container" class="vs-con-loading__container">
    <vs-table :data="repaymentTableData" :noDataText="repaymentTableNoDataText" search>
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="exportXls(repaymentTableData, columnDefs)" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="exportCsv(repaymentTableData, columnDefs)" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>
          <vs-button class="p-3" @click="refreshRepaymentTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="repaymentTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ repaymentTablePagingInfo }}</span>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="installmentDate">
          {{ this.$t('INSTALLMENT_DATE') }}
        </vs-th>
        <vs-th sort-key="installmentAmount">
          {{ this.$t('INSTALLMENT_AMOUNT') }}
        </vs-th>
        <vs-th sort-key="principalAmount">
          {{ this.$t('PRINCIPAL_AMOUNT') }}
        </vs-th>
        <vs-th sort-key="interestAmount">
          {{ this.$t('INTEREST_AMOUNT') }}
        </vs-th>
        <vs-th sort-key="outstandingAmount">
          {{ this.$t('OUTSTANDING_AMOUNT') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].installmentDate">
            {{ data[index].installmentDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].installmentAmount">
            {{ data[index].installmentAmount | currency('') }}
          </vs-td>
          <vs-td :data="data[index].principalAmount">
            {{ data[index].principalAmount | currency('') }}
          </vs-td>
          <vs-td :data="data[index].interestAmount">
            {{ data[index].interestAmount | currency('') }}
          </vs-td>
          <vs-td :data="data[index].outstandingAmount">
            <span :class="positiveNumberStyle(data[index].outstandingAmount)">{{ data[index].outstandingAmount | currency('') }}</span>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import LoanService from '@/services/loan/LoanService'

export default {
  name: 'loan-repayment',
  mixins: [AlertMixin, LoadingMixin, NumberStyleMixin, ListLayoutMixin],
  props: {
    loanNumber: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      repaymentTableData: [],
      repaymentTableContainer: 'repayment-loading-container',
      repaymentTableBusy: false,
      repaymentTableTotalRows: 0,
      repaymentTableCurrentPage: 1,
      repaymentTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('INSTALLMENT_DATE'),
        field: 'installmentDate',
        format: 'Date'
      }, {
        headerName: this.$t('INSTALLMENT_AMOUNT'),
        field: 'installmentAmount',
        format: 'Decimal'
      }, {
        headerName: this.$t('PRINCIPAL_AMOUNT'),
        field: 'principalAmount',
        format: 'Decimal'
      }, {
        headerName: this.$t('INTEREST_AMOUNT'),
        field: 'interestAmount',
        format: 'Decimal'
      }, {
        headerName: this.$t('OUTSTANDING_AMOUNT'),
        field: 'outstandingAmount',
        format: 'Decimal'
      }]
    }
  },
  computed: {
    repaymentTableNoDataText: function () {
      return this.repaymentTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    repaymentTableTotalPages: function () {
      return Math.ceil(this.repaymentTableTotalRows / this.repaymentTablePerPage)
    },
    repaymentTablePagingInfo: function () {
      return this.pagingInfo(this.repaymentTableCurrentPage, this.repaymentTablePerPage, this.repaymentTableTotalRows)
    }
  },
  methods: {
    loadRepaymentTableData (loadingContainerId) {
      let self = this
      this.repaymentTableBusy = true
      this.repaymentTableContainer = loadingContainerId || this.repaymentTableContainer
      this.startLoading(this.repaymentTableContainer)
      LoanService.loanRepayments({
        loanNumber: this.loanNumber
      }).then(function (response) {
        self.stopLoading(self.repaymentTableContainer)
        self.repaymentTableBusy = false
        self.repaymentTableData = response.data.repaymentList || []
        self.repaymentTablePerPage = response.data.totalRows
        self.repaymentTableTotalRows = response.data.totalRows
      }).catch(function (error) {
        self.stopLoading(self.repaymentTableContainer)
        self.repaymentTableBusy = false
        self.showDangerAlert(error.response.data.errors)
      })
    },
    refreshRepaymentTable () {
      this.loadRepaymentTableData()
    }
  },
  mounted () {
    this.loadRepaymentTableData()
  },
  watch: {
    repaymentTableCurrentPage: function () {
      this.refreshRepaymentTable()
    }
  }
}
</script>
