import Endpoint from '@/services/Endpoint'

export default {
  loans (payload) {
    return Endpoint().get('/s1/connect/loan', {
      params: payload
    })
  },
  loanDetail (payload) {
    return Endpoint().get(`/s1/connect/loan/${payload.loanNumber}/detail`, {
      params: payload
    })
  },
  loanRepayments (payload) {
    return Endpoint().get(`/s1/connect/loan/${payload.loanNumber}/repayment`, {
      params: payload
    })
  }
}
